import React from 'react'
import SectionTitle from '../Title'
import { Link } from 'react-router-dom'
import './style.scss'

// images
import blog1 from '../../images/blog/1.jpg'
import blog2 from '../../images/blog/2.jpg'
import blog3 from '../../images/blog/3.jpg'
import blog4 from '../../images/blog/4.jpg'
import blog5 from '../../images/blog/5.jpg'
import blog6 from '../../images/blog/6.jpg'
import blog7 from '../../images/blog/7.jpg'
import blog8 from '../../images/blog/8.jpg'
import blog9 from '../../images/blog/9.jpg'
import blog10 from '../../images/blog/10.jpg'
import avatar from '../../images/blog-page/6.jpg'

const blogs = [
    {
        images: blog1, title: 'TDMホールディングス', meta: {
            avatar: avatar, name: 'Taoru', time: 'Nov 15,2020'
        }
    },
    {
        images: blog2, title: 'TDMモーター', meta: {
            avatar: avatar, name: 'Nettu', time: 'Nov 15,2020'
        }
    },
    {
        images: blog3, title: 'Mascorganism', meta: {
            avatar: avatar, name: 'RAO', time: 'Nov 15,2020'
        }
    },
    {
        images: blog4, title: '帝東製菓', meta: {
            avatar: avatar, name: 'Nansiro', time: 'Nov 15,2020'
        }
    },
    {
        images: blog5, title: '枡肉食品', meta: {
            avatar: avatar, name: 'Buro', time: 'Nov 15,2020'
        }
    },
    {
        images: blog6, title: 'TDM.com', meta: {
            avatar: avatar, name: 'Kajiura', time: 'Nov 15,2020'
        }
    },
    {
        images: blog7, title: 'ピュアクララ', meta: {
            avatar: avatar, name: 'Buke', time: 'Nov 15,2020'
        }
    },
    {
        images: blog8, title: 'TTSアクアリウム', meta: {
            avatar: avatar, name: 'Yoyo', time: 'Nov 15,2020'
        }
    },
    {
        images: blog9, title: 'MMNホールディングス', meta: {
            avatar: avatar, name: 'Norio', time: 'Nov 15,2020'
        }
    },
    {
        images: blog10, title: 'ドラまくら', meta: {
            avatar: avatar, name: 'Udon', time: 'Nov 15,2020'
        }
    },
]


const BlogArea = ({ className, title, subTitle }) => {
    return (
        <div className={className}>
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <SectionTitle
                            title={title}
                            subTitle={subTitle}
                        />
                    </div>
                    {blogs.map((blog, i) => (
                        <div key={i} className="col-lg-4 col-sm-6 col-12" >
                            <div className="blogWrap">
                                <div className="blogImage">
                                    <img src={blog.images} alt="" />
                                </div>
                                <div className="blogContent">
                                    <h3>{blog.title}</h3>
                                    <ul className="blogMeta">
                                        <li><img src={blog.meta.avatar} alt="" /></li>
                                        <li>{blog.meta.name}</li>
                                        <li>{blog.meta.time}</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}
export default BlogArea
